import $ from 'jquery';
import extend from 'extend';
import createFocusTrap from 'focus-trap';

/**
 * function that calculates the scrollbar width
 */

function getVerticalScrollbarWidth() {
    // Creating a div element for helping to calculate scrollbar width
    const scrollDiv = document.createElement('div');
    scrollDiv.style.cssText =
        'width: 100px; height: 100px; overflow: scroll; position: absolute; top: -9999px;';

    // Appending the div to the body to measure scrollbar width
    document.body.appendChild(scrollDiv);
    const scrollbarWidth = scrollDiv.offsetWidth - scrollDiv.clientWidth;

    // Remove the div from the body
    document.body.removeChild(scrollDiv);

    // adds scrollbar width as variable to root so can be used by CSS styles
    let root = document.documentElement;
    root.style.setProperty('--scrollbar-width', scrollbarWidth + 'px');

    return scrollbarWidth;
}

/**
 * function that enables / disables body scroll when modal opens or closes. As the scrollbar disappears
 * to lock scrolling, a margin gets added in place of where the scrollbar use to be
 * to prevent content from jumping
 *
 */

function enableBodyScroll() {
    document.documentElement.classList.remove('no-scroll');
    document.querySelector('.back-to-top').setAttribute('hidden', 'false');
}

function disableBodyScroll() {
    getVerticalScrollbarWidth();
    document.querySelector('.back-to-top').setAttribute('hidden', 'true');
    document.documentElement.classList.add('no-scroll');
}

function closeDialog(focusBack = true) {
    if (current.dlg) {
        current.focusTrap.deactivate();
        current.dlg.remove();
        current.dlg = null;
    }
    if (focusBack && current.focusBackTo) {
        current.focusBackTo.focus();
        current.focusBackTo = null;
    }

    enableBodyScroll();
}

const DEFAULT_OPTS = {
    closeOnOverlay: true,
    closeButton: true,
    identifyFocusBackTo: true,
    className: null,
};

const current = {
    focusTrap: null,
    dlg: null,
    focusBackTo: null,
};

export function openModalDialog(title, content, opts = {}) {
    closeDialog(false);
    let finalOptions = extend({}, DEFAULT_OPTS, opts);

    if (finalOptions.identifyFocusBackTo) {
        current.focusBackTo = $(':focus');
    }

    let dlg = (current.dlg = $('<div></div>')
        .addClass('dialog')
        .attr('tabindex', '0')
        .appendTo($('body')));

    if (finalOptions.className) {
        dlg.addClass(finalOptions.className);
    }

    dlg.on('keydown', evt => {
        if (evt.which === 27) {
            closeDialog();
            evt.preventDefault();
        }
    });

    let box = $('<div></div>')
        .addClass('dialog__box')
        .appendTo(dlg);

    let heading = $('<div></div>')
        .addClass('dialog__box__heading')
        .html(title)
        .appendTo(box);

    $('<div></div>')
        .addClass('dialog__box__content')
        .append(content)
        .appendTo(box);

    if (finalOptions.closeOnOverlay) {
        dlg.on('click', evt => {
            if (evt.target === dlg[0]) {
                closeDialog();
            }
        });
    }

    if (finalOptions.closeButton) {
        let button = $('<a></a>')
            .attr('href', '#')
            .attr('aria-label', 'Close')
            .attr('role', 'button')
            .addClass('dialog__box__heading__close')
            .append(
                $('<span></span>').addClass('dialog__box__heading__close__icon')
            );

        button.on('click', evt => {
            evt.preventDefault();
            closeDialog();
        });

        heading.append(button);
    }

    dlg.focus();

    current.focusTrap = createFocusTrap(dlg[0]).activate();

    disableBodyScroll();
}
