import $ from 'jquery';

const className = 'ghost';

function launch(el) {
    let ghostButtons = $(el);

    for (let ghostButton of ghostButtons) {
        ghostButton.addEventListener('mouseover', () => {
            ghostButton.classList.add('ghost--active');
            ghostButton.removeEventListener('mouseover', () => {});
        });
    }
}

export default {
    launch,
    className,
};
