import './polyfills.js'; // Ensures backward compatibility for older browsers
import './plugins/plugins'; // Loads plugins used by the project
import patterns from './component-list'; // List of components or patterns to be initialized
import { parametersToObject } from './js-utils/util.js'; // Utility function to convert URL parameters to an object
import './components/linkIcons/linkIcons.js'; // Handles link icons component
import './components/video-preview/video-wrap.js'; // Handles video preview components
import { devcorate } from './components/devcorate/devcorate.js'; // Function to decorate elements based on certain parameters

// Error handling wrapper to safely execute a function
function tryCatch(f) {
    try {
        f(); // Try to run the function
    } catch (e) {
        // If an error occurs, log it to the console
        if (console && console.error) {
            console.error(e);
        }
    }
}

// Function to launch a given pattern or component
function launchPattern(pattern) {
    if (typeof pattern === 'function') {
        // If the pattern is a function, execute it safely
        tryCatch(pattern);
    } else if (pattern.className) {
        // If the pattern has a className, initialize it on all matching elements
        let { className, launch } = pattern;
        // Select all DOM elements matching the class, excluding those already initialized
        const elements = document.querySelectorAll(`.${className}:not(.${className}-njs)`);
        // Launch the component for each selected element
        elements.forEach((element) => {
            tryCatch(() => launch(element));
        });
    }
}

// Run once the DOM is fully loaded
document.addEventListener(
    'DOMContentLoaded', 
    () => {
    // Initialize all patterns or components
    patterns.forEach(launchPattern);

    // Convert URL parameters into an object
    const parameters = parametersToObject(location.search);

    // If the 'test' parameter exists, apply the devcorate function to the body element
    parameters['test'] &&
        devcorate(
            document.querySelector('body'),
            'test',
            parameters['test']
        );

    // If the 'prod' parameter exists, apply the devcorate function for production mode
    parameters['prod'] &&
        devcorate(
            document.querySelector('body'),
            'prod',
            parameters['prod']
        );
});
