import $ from 'jquery';
import { browserDetect } from './browser-detect';

const FEATURES = ['csspositionsticky', 'video'];

const FEATURE_SUPPORT = FEATURES.reduce(
    (acc, key) => {
        acc[key] = $('html').hasClass(key);
        return acc;
    },
    {
        intersectionObserver: typeof IntersectionObserver === 'function',
        version: browserDetect(),
    }
);

export default FEATURE_SUPPORT;
