const PARSE_REGEX = /^\s*([0-9]) ([^\s]+) [0-9] (.+)$/;

const currentURL = document.getElementById('page_urls').innerHTML;
																						            
function loadData() {
    if (currentURL.includes('ac.uk/staff-hub') || currentURL.includes('ac.uk/alumni') ) {
        return document.getElementById('nav-mobile__data__tertiary').innerHTML;  // used for Hidden menu as root
    } else {
        return document.getElementById('nav-mobile__data').innerHTML; // Global tree
    }
}

function loadUrls() {
    return document.getElementById('page_urls').innerHTML;
}

function getPath(url) {
    return url.replace(/^https?:\/\/[^/]*/, '');
}

function matchLevel(path, currentPath) {
    if (path === currentPath) {
        return 2;
    } else if (currentPath.indexOf(path + '/') === 0) {
        return 1;
    } else {
        return 0;
    }
}

/*
pareseNav function to return asset tree Object and current asset tree Array
This data is used in VerticalMenuHelper() which creates left-hand navigation and also called in NavMobile() which creates
mobile navigation
*/

function parseNav(data, currentUrl) {
    let tree = {
        name: null,
        index: null,
        url: null,
        type: null,
        level: null,
        children: [],
    };

    let currentPath = getPath(currentUrl);

    let ancestors = [];

    data.split(/\r?\n/).forEach(line => {
        let match = line.match(PARSE_REGEX);
        if (match) {
            let [, levelStr, url, name] = match;
            let level = parseInt(levelStr);
            let type = matchLevel(getPath(url), currentPath);
            let item = { level, type, url, name, children: [], index: 0 };

            if (level > 0) {
                let parent = ancestors[level - 1];
                item.index = parent.children.length;
                parent.children.push(item);
            } else {
                tree = item;
            }

            ancestors[level] = item;
        }
    });

    let currentHead = tree;
    let current = [];
    while (currentHead && currentHead.children.length > 0) {
        current.push(currentHead);
        currentHead = findCurrent(currentHead.children);
    }

    return { tree, current };
}

function findCurrent(list) {
    for (let i = 0; i < list.length; i++) {
        if (list[i].type > 0) {
            return list[i];
        }
    }

    return null;
}

const { tree, current } = parseNav(loadData(), loadUrls());

export { tree, current, currentURL };
