export function identifyVariations(element, className) {
    const regex = new RegExp(`^${className}--(.+)$`);

    return element
        .attr('class')
        .split(' ')
        .map(v => {
            let match = v.trim().match(regex);
            return match && match[1];
        })
        .filter(v => !!v);
}

export function variationClassNames(className, variations) {
    return [className, ...variations.map(v => `${className}--${v}`)].join(' ');
}

export function applyVariations(element, className, variations) {
    element.addClass(variationClassNames(className, variations));
}
