import $ from 'jquery';
import extend from 'extend';
import createYoutubePlayer from '../../js-utils/youtube-player';
import scolledIntoView from '../../js-utils/scrolled-into-view';

const DEFAULT_PARAMS = {
    frameBorder: '0',
    playerVars: {
        rel: 0,
    },
};

export default function loadYoutubeVideo(wrapper, { id, params }) {
    let iframeId = `ytev-${id}`;
    let link = null;

    link = wrapper.children('a');
    let fallbackParams = {
        width: link.width(),
        height: link.height(),
    };

    let playerParams = extend({}, DEFAULT_PARAMS, fallbackParams, params, {
        videoId: id,
        events: {
            onInit: () => {
                $('<div/>')
                    .attr('id', iframeId)
                    .attr(fallbackParams)
                    .insertBefore(link);
                wrapper.addClass('video-preview--loading');
            },
            onReady: event => {
                let player = event.target;
                let iframe = link.siblings('iframe');

                wrapper.removeClass('video-preview--loading');
                wrapper.addClass('media--youtube');
                wrapper.children(`:not(#${iframeId})`).css('display', 'none');
                player.playVideo();

                let yttitle = player.getVideoData().title;
                let ytURL = player.getVideoUrl();
                dataLayer.push({
                    event: 'play-Youtube-video',
                    'gtm.videoTitle': yttitle,
                    'gtm.videoUrl': ytURL,
                });
                $(window).on('scroll', () => {
                    if (scolledIntoView(iframe) < 0.5) {
                        player.pauseVideo();
                    }
                });
            },
        },
    });

    createYoutubePlayer(iframeId, playerParams);
}
