import $ from 'jquery';
import { contentCn } from './element-classnames';

export default function prepareContent(className, widget) {
    let contentClassName = contentCn(className);
    let contentElement = widget.children(`.${contentClassName}`);

    if (contentElement.length === 0) {
        contentElement = $('<div></div>')
            .addClass(contentClassName)
            .append(widget.children())
            .appendTo(widget);
    }

    return contentElement;
}
