import $ from 'jquery';

function launch(el) {
  $(el).find( '.image-credit__text' ).toggle();
}

$( '.image-credit__button' ).click(function() {
  $(this).siblings( '.image-credit__text' ).toggle();
  
  if ( $(this).attr('aria-pressed') == 'false' ) {
    $(this).attr('aria-pressed', 'true');
  } else {
    $(this).attr('aria-pressed', 'false');
  }
});

const className = 'news__image-credit__thumbnail';

export default {
    launch,
    className,
};
