import $ from 'jquery';
/*
    This will only run for standard content pages and only in the container
    It will add an external link to any a tag that is outside the CASS domain

    Specific cases have been made to work on span tags for pathway cards and hallway items so the external link icon occurs after the title
    Therefore must be applied to span tag so it's in the title
*/

if ($('body').is('[class*="scp"]')) {
    $('a').each(function() {
        if (
            $(this)
                .parents()
                .hasClass('left-hand-navigation-grid')
        ) {
            //  If link is external add class external which will allow FA icon (linkIcons.scss)
            if (
                checkLinkExternal(this) &&
                !checkImage(this) &&
                !$(this).hasClass('pathway-card__anchor') &&
                !$(this)
                    .parents()
                    .hasClass('hallway__item')
            ) {
                $(this).addClass('link__external');
            }

            // If link contains pdf add class pdf which will alow FA pdf icon (linkIcons.scss)
            if (checkLinkPDF(this) && !$(this).hasClass('cta')) {
                $(this).addClass('link__pdf');
            }

            // For pathway cards and hallway items as both title is in a span rather than in the actual a tag
            if (
                $(this).hasClass('pathway-card') ||
                ($(this)
                    .parents()
                    .hasClass('hallway__item') &&
                    !$(this).hasClass('pathway-card__left') &&
                    !$(this).hasClass('pathway-card__right'))
            ) {
                if (checkLinkExternal(this)) {
                    $(this)
                        .find('span')
                        .addClass('link__external');
                }
            }
        }
    });
}

/* 
    checkLinkExternal: checks if link goes outside of domain (CASS)
    checkLinkPDF: checks if link contains .pdf
    checkImage: checks if image is wrapped in an A tag
*/
function checkLinkExternal(link) {
    if (
        !(location.hostname === link.hostname || !link.hostname.length) &&
        !$(link).hasClass('cta') &&
        !$(link)
            .parents()
            .hasClass('connect-social__card') &&
        !$(link)
            .parents()
            .hasClass('socialmedia-card')
    ) {
        return true;
    }
    return false;
}

function checkLinkPDF(linkPDF) {
    if (linkPDF.href.indexOf('.pdf') != -1) {
        return true;
    }
    return false;
}

function checkImage(linkImage) {
    if ($(linkImage).children('img').length) {
        return true;
    }
    return false;
}
