import $ from 'jquery';

function launch(el) {
    let widget = $(el);

    let data = widget
        .find('script')
        .html()
        .split('\n')
        .map(line => line.trim().split(' '))
        .filter(item => item.length > 2)
        .map(([selected, url, ...rest]) => ({
            selected,
            url,
            name: rest.join(' '),
        }));

    let selectedIndex = data.findIndex(({ selected }) => selected === '1');
    let direction = 'right';
    let other = data[selectedIndex === 0 ? 1 : 0];
    let current = data[selectedIndex];

    let anchor = $('<a></a>')
        .addClass(`url-slide url-slide--${direction}`)
        .attr('href', other.url)
        .appendTo(widget);

    $('<div></div>')
        .append(
            $('<span></span>')
                .addClass('url-slide__current')
                .html(current.name)
        )
        .append($('<span></span>').addClass('url-slide__icon'))
        .append(
            $('<span></span>')
                .addClass('url-slide__next')
                .html(other.name)
        )
        .appendTo(anchor);
}

const className = 'url-slide';

export default {
    launch,
    className,
};
