import $ from 'jquery';
import browserSupport from '../../js-utils/browser-support';
import { launchAnimation } from './launch-animation';

function launch(el) {
    if (browserSupport.intersectionObserver) {
        const widget = $(el);
        const types = widget
            .attr('class')
            .split(' ')
            .map(v => v.trim())
            .filter(v => !!v && v.match(/animate--.+/))
            .map(v => v.substr('animate--'.length));

        launchAnimation(widget, ...types);
    }
}

const className = 'animate';

export default {
    launch,
    className,
};
