/**
 * Turns string boolean into real boolean.
 *
 * @param {string} s - A string that may be 'true'.
 * @returns {boolean} True if 'true'.
 */
export function toBool(s) {
    return s === 'true' ? true : false;
}

export const partialMatchFn = (m) => (s) => s.indexOf(m) < 0,
    fullMatchFn = (m) => (s) => s !== m;

/**
 * Remove a class name from an HTML element.
 *
 * @param {HTMLElement} elem - An HTML element.
 * @param {string} className - A string representing the class to remove.
 * @param {boolean} [removePartials] - If true, remove partially matching classes.
 */
export function removeClass(elem, className, removePartials) {
    const matchFn = removePartials
        ? partialMatchFn(className)
        : fullMatchFn(className);
    elem.className = elem.className
        .split(/\s+/)
        .filter((s) => matchFn(s))
        .join(' ');
}

export function reduceMotion() {
    const reducedMotionQuery = window.matchMedia(
        '(prefers-reduced-motion: reduce)'
    );
    return reducedMotionQuery.matches ? true : false;
}

export function verticallyInWindow(elem) {
    return elem.getBoundingClientRect().top >= 0 &&
        elem.getBoundingClientRect().top <= window.innerHeight
        ? true
        : false;
}

export function appendAll(elem, children) {
    children.forEach((child) => elem.appendChild(child));
}

/**
 * Create elements containing classes, content and any other attributes
 *
 * @param {string} type - Type of element to create, e.g. "div", "button".
 * @param {array} attributes - Array of objects specifying which attributes to assign to the element i.e. {label : "class", val : "container"}
 */
export function createHTMLElement(type, attributes) {
    let el = document.createElement(type);
    attributes.map(function (att) {
        att.label === 'content'
            ? el.appendChild(document.createTextNode(att.val))
            : att.label === 'html'
            ? (el.innerHTML = att.val)
            : el.setAttribute(att.label, att.val);
    });
    return el;
}