import $ from 'jquery';

function launch(el) {
    const link = $('.carousel__control__button__link');
    const widget = $(el);
    const firstCard = widget.find($('div[data-card="1"]'));

    firstCard.addClass('active');

    link.each(function() {
        $(this).click(function(e) {
            e.preventDefault();
            let buttonID = $(this).data('button');
            $('div[data-card = "' + buttonID + '"]').each(function() {
                if ($(this).hasClass('active')) {
                    return true;
                } else {
                    $('div.active').removeClass('active');
                    $(this).addClass('active');
                }
            });
            $('a[data-button = "' + buttonID + '"]').each(function() {
                if ($(this).hasClass('active')) {
                    return true;
                } else {
                    $('a.active').removeClass('active');
                    $(this).addClass('active');
                    $(this).blur();
                }
            });
        });
    });
}

const className = 'carousel';

export default {
    launch,
    className,
};
