import $ from 'jquery';
import {
    menuCn,
    menuWrapperCn,
    menuItemCn,
    menuItemLabelCn,
} from './element-classnames';
import stickybits from 'stickybits';
import browserSupport from '../../../js-utils/browser-support';

export default function prepareTabsHeadings(
    className,
    widget,
    i,
    headings,
    content,
    sticky,
    accordionId
) {
    let tabsBar = $('<div></div>').addClass(menuCn(className));

    headings.forEach(({ text, id }) => {
        let anchor = $('<a href="#"></a>')
            .addClass(menuItemCn(className))
            .addClass('inactive')
            .attr('id', `tab-${accordionId}-header-${id}`)
            .attr('data-id', id);
        $('<span></span>')
            .addClass(menuItemLabelCn(className))
            .html(text)
            .appendTo(anchor);

        anchor.appendTo(tabsBar);
    });

    let wrapper = $('<div></div>')
        .addClass(menuWrapperCn(className))
        .append(tabsBar);
    let containerClass = `${menuCn(className)}-fullwidth`;
    $('<div></div>')
        .addClass(containerClass)
        .append(wrapper)
        .insertBefore(content);

    if (sticky && browserSupport.csspositionsticky) {
        stickybits(`.${containerClass}`, { useStickyClasses: true });
    }

    return tabsBar;
}
