import $ from 'jquery';
import scrollTo from '../../../js-utils/scroll-to';
import scrolledAboveView from '../../../js-utils/scrolled-above-view';
function genActivateItem(widget, headingAnchors, allAnchors) {

    return (id, scroll) => {
        allAnchors
            .filter(`[data-id="${id}"].inactive`)
            .removeClass('inactive')
            .addClass('active')
            .attr('aria-selected', 'true')
            .attr('aria-expanded', 'true');

        allAnchors
            .filter('.active')
            .not(`[data-id="${id}"]`)
            .removeClass('active')
            .addClass('inactive')
            .attr('aria-selected', 'false')
            .attr('aria-expanded', 'false');

        let heading = headingAnchors.filter(`[data-id="${id}"]`);

        if (scroll === 'accordion' && scrolledAboveView(heading)) {
            scrollTo(heading);
        } else if (scroll === 'tab' && scrolledAboveView(widget)) {
            scrollTo(widget);
        }
    };
}

export function deactivateAll(allAnchors) {
    allAnchors
        .filter('.active')
        .removeClass('active')
        .addClass('inactive')
        .attr('aria-selected', 'false')
        .attr('aria-expanded', 'false');
}

export default function prepareLinks(
    widget,
    headings,
    menu = null,
    activateInitial = null
) {
    let headingAnchors = headings.reduce(
        (acc, { anchor }) => acc.add(anchor),
        $()
    );

    let allAnchors = menu
        ? headingAnchors.add(menu.find('a[data-id]'))
        : headingAnchors;

    let activateItem = genActivateItem(widget, headingAnchors, allAnchors);

    if (menu) {
        let menuAnchors = menu.find('a[data-id]');

        menuAnchors.on('keydown', function(evt) {
            let moveTo = -1;
            let anchor = $(this);

            if (evt.which === 37) {
                // left
                moveTo =
                    (anchor.index() + menuAnchors.length - 1) %
                    menuAnchors.length;
            } else if (evt.which === 39) {
                moveTo = (anchor.index() + 1) % menuAnchors.length;
            }

            if (moveTo >= 0) {
                menuAnchors.eq(moveTo).focus();
                evt.preventDefault();
            }
        });
    }

    /***** SESSION ANCHOR VALUES *****/

    // Capture top-level tab anchor click ID
    let parentAccordionTab = $(
        '.accordion-tabs--top-level-layout .accordion-tabs__content .accordion-tabs__content__heading-anchor, .accordion-tabs__menu-fullwidth .accordion-tabs__menu-wrapper .accordion-tabs__menu a'
    );
    parentAccordionTab.on('click', function() {
        // ID of selected tab
        let selectedTab = $(this).attr('data-id');

        // Store selected tab data ID in session storage
        sessionStorage.setItem('parent-anchor-data-id', selectedTab);
    });

    // Capture selected nested accordion data-id
    let nestedAccordion = $(
        '.accordion-tabs__content .accordion__content__heading-anchor'
    );
    nestedAccordion.on('click', function() {
        let selectedNestedAnchor = $(this).attr('data-id');
        sessionStorage.setItem(
            'nested-accordion-data-id',
            selectedNestedAnchor
        );
    });

    // Capture selected nested tab data-id
    let nestedTab = $('.accordion-tabs__content .tabs__menu__item');
    nestedTab.on('click', function() {
        let selectedNestedTab = $(this).attr('data-id');
        sessionStorage.setItem('nested-tab-data-id', selectedNestedTab);
    });

    // Get selected anchor 'data-id' from session storage
    let sessionParentAnchorId = sessionStorage.getItem('parent-anchor-data-id');
    let sessionNestedAccordionId = sessionStorage.getItem(
        'nested-accordion-data-id'
    );
    let sessionNestedTabId = sessionStorage.getItem('nested-tab-data-id');

    /***** END SESSION ANCHOR VALUES *****/

    // Find all elements on page with 'data-id' value matching sessionParentAnchorId
    const matchingIds = document.querySelectorAll(
        `[data-id='${sessionParentAnchorId}']`
    );

    // If there is a previously selected anchor in the session
    if (sessionParentAnchorId) {
        /***** SELECTOR VARIABLES *****/

        // Tab anchors
        const tabFirstAnchor = $('.accordion-tabs__menu__item:eq(0)');
        const tabNotFirstAnchor = $('.accordion-tabs__menu__item:not(:eq(0))');

        // Accordion anchors
        const accordionFirstAnchor = $(
            '.accordion-tabs__content__heading-anchor:eq(0)'
        );
        const accordionNotFirstAnchor = $(
            '.accordion-tabs__content__heading-anchor:not(:eq(0))'
        );

        // Accordion and tab anchors
        const firstAnchorContent = $(
            '.accordion-tabs__content .accordion-tabs__content__group:eq(0)'
        );
        const notfirstAnchorContent = $(
            '.accordion-tabs__content .accordion-tabs__content__group:not(:eq(0))'
        );

        // New anchor clicks
        let newAnchorDataId;

        /***** END SELECTOR VARIABLES *****/

        // DESKTOP: Set active tab anchor menu items to correct class based on most recent session activity
        const tabAnchors = document.getElementsByClassName(
            'accordion-tabs__menu__item'
        );
        [].forEach.call(tabAnchors, function(e) {
            if (e.getAttribute('data-id') == sessionParentAnchorId) {
                e.className = 'accordion-tabs__menu__item';
                e.setAttribute('aria-selected', 'true');
                e.setAttribute('aria-expanded', 'true');
            } else if (e.getAttribute('data-id') == '0') {
                e.className = 'accordion-tabs__menu__item';
            } else {
                e.className = 'accordion-tabs__menu__item inactive';
                e.setAttribute('aria-selected', 'false');
                e.setAttribute('aria-expanded', 'false');
            }
        });

        // DESKTOP: Tab anchor menu interaction
        $('.accordion-tabs__menu__item').click(function() {
            // Remove stored nested anchor items so these values are only retained with the final parent anchor selection
            sessionStorage.removeItem('nested-accordion-data-id');
            sessionStorage.removeItem('nested-tab-data-id');

            // If sessionParentAnchorId value is 0, show this content
            if (sessionParentAnchorId == 0) {
                $('.container.accordion-tabs__content__group:first').show();
            } else {
                $('.container.accordion-tabs__content__group:first').hide();
            }

            // Capture data-id attribute of clicked menu item
            newAnchorDataId = $(this).attr('data-id');

            if (newAnchorDataId == '0') {
                // Content behaviour
                firstAnchorContent.css('display', 'block');
                notfirstAnchorContent.css('display', 'none');

                // Menu heading behaviour
                tabFirstAnchor.attr('aria-selected', 'true');
                tabFirstAnchor.attr('aria-expanded', 'true');
                tabNotFirstAnchor.attr('aria-selected', 'false');
                tabNotFirstAnchor.attr('aria-expanded', 'false');
            } else {
                // Content behaviour
                $(
                    `.accordion-tabs__content .accordion-tabs__content__group:eq(${newAnchorDataId})`
                ).css('display', 'block');
                $(
                    `.accordion-tabs__content .accordion-tabs__content__group:not(:eq(${newAnchorDataId}))`
                ).css('display', 'none');

                // Menu heading behaviour
                $(`.accordion-tabs__menu__item:eq(${newAnchorDataId})`).attr({
                    'aria-selected': 'true',
                    'aria-expanded': 'true',
                });
                $(
                    `.accordion-tabs__menu__item:not(:eq(${newAnchorDataId}))`
                ).attr({
                    'aria-selected': 'false',
                    'aria-expanded': 'false',
                });
                firstAnchorContent.css('display', 'none');
            }
        });

        // Nested accordion behaviour
        if (sessionNestedAccordionId) {
            const nestedAnchors = $(
                `.accordion-tabs__content__group:eq(${sessionParentAnchorId}) a.accordion__content__heading-anchor`
            );
            [].forEach.call(nestedAnchors, function(e) {
                if (e.getAttribute('data-id') == sessionNestedAccordionId) {
                    e.className = 'accordion__content__heading-anchor active';
                    e.setAttribute('aria-selected', 'true');
                    e.setAttribute('aria-expanded', 'true');
                } else if (e.getAttribute('data-id') == '0') {
                    e.className = 'accordion__content__heading-anchor inactive';
                } else {
                    e.className = 'accordion__content__heading-anchor inactive';
                    e.setAttribute('aria-selected', 'false');
                    e.setAttribute('aria-expanded', 'false');
                }
            });
        }
        // Nested tabs behaviour
        if (sessionNestedTabId) {
            const nestedTabAnchors = $(
                `.accordion-tabs__content__group:eq(${sessionParentAnchorId}) a.tabs__menu__item`
            );
            [].forEach.call(nestedTabAnchors, function(e) {
                if (e.getAttribute('data-id') == sessionNestedTabId) {
                    e.className = 'tabs__menu__item active';
                    e.setAttribute('aria-selected', 'true');
                    e.setAttribute('aria-expanded', 'true');
                } else if (e.getAttribute('data-id') == '0') {
                    e.className = 'tabs__menu__item inactive';
                    e.setAttribute('aria-selected', 'false');
                    e.setAttribute('aria-expanded', 'false');
                } else {
                    e.className = 'tabs__menu__item inactive';
                    e.setAttribute('aria-selected', 'false');
                    e.setAttribute('aria-expanded', 'false');
                }
            });
        }

        const accordionAnchors = document.getElementsByClassName(
            'accordion-tabs__content__heading-anchor'
        );
        [].forEach.call(accordionAnchors, function(e) {
            if (e.getAttribute('data-id') == sessionParentAnchorId) {
                e.className = 'accordion-tabs__content__heading-anchor';
                e.setAttribute('aria-selected', 'true');
                e.setAttribute('aria-expanded', 'true');
            } else if (e.getAttribute('data-id') == '0') {
                e.className = 'accordion-tabs__content__heading-anchor';
            } else {
                e.className =
                    'accordion-tabs__content__heading-anchor inactive';
                e.setAttribute('aria-selected', 'false');
                e.setAttribute('aria-expanded', 'false');
            }
        });

        // Loop through elements with matching 'data-id' values
        [].forEach.call(matchingIds, function(elem) {
            let parent = elem.parentElement.className;

            // Limit behaviour to accordion-tabs
            if (parent == 'accordion-tabs__content') {
                elem.className = 'accordion-tabs__content__heading-anchor';
                elem.setAttribute('aria-selected', 'true');
                elem.setAttribute('aria-expanded', 'true');

                // If previously selected tab was the first tab, show its content
                if (sessionParentAnchorId == 0) {
                    firstAnchorContent.show();
                    accordionFirstAnchor.attr('aria-selected', 'true');
                    accordionFirstAnchor.attr('aria-expanded', 'true');
                } else {
                    firstAnchorContent.hide();
                    accordionFirstAnchor.attr('aria-selected', 'false');
                    accordionFirstAnchor.attr('aria-expanded', 'false');
                }

                // MOBILE: tab clicks
                $('.accordion-tabs__content__heading-anchor').click(function() {
                    newAnchorDataId = $(this).attr('data-id');

                    /***** MOBILE *****/
                    if ($(this).attr('aria-selected') == 'true') {
                        $(this).attr('aria-selected', 'false');
                        $(this).attr('aria-expanded', 'false');
                        $(this).attr(
                            'class',
                            'accordion-tabs__content__heading-anchor active'
                        );
                        $(
                            `.accordion-tabs__content .accordion-tabs__content__group:eq(${newAnchorDataId})`
                        ).css('dislay', 'none');
                    } else if ($(this).attr('aria-selected') == 'false') {
                        $(this).attr('aria-selected', 'true');
                        $(this).attr('aria-expanded', 'true');
                        $(
                            `.accordion-tabs__content .accordion-tabs__content__group:eq(${newAnchorDataId})`
                        ).css('dislay', 'block');
                        $(this).attr(
                            'class',
                            'accordion-tabs__content__heading-anchor inactive'
                        );
                    }

                    if (
                        newAnchorDataId == sessionParentAnchorId &&
                        $(this).attr('aria-expanded') == 'true'
                    ) {
                        $(this).attr('aria-selected', 'false');
                        $(this).attr('aria-expanded', 'false');
                        $(this).attr(
                            'class',
                            'accordion-tabs__content__heading-anchor inactive'
                        );
                    }

                    if (newAnchorDataId != sessionParentAnchorId) {
                        $(
                            `.accordion-tabs__content .accordion-tabs__content__heading-anchor:eq(${sessionParentAnchorId})`
                        ).attr({
                            'aria-expanded': 'false',
                            'aria-selected': 'false',
                            class:
                                'accordion-tabs__content__heading-anchor inactive',
                        });
                    }

                    if (
                        $(this).attr('data-id') == '0' &&
                        $(this).attr('aria-selected') == 'true'
                    ) {
                        firstAnchorContent.show();
                        notfirstAnchorContent.hide();
                        accordionNotFirstAnchor.attr('aria-selected', 'false');
                        accordionNotFirstAnchor.attr('aria-expanded', 'false');
                        $(
                            `.accordion-tabs__content .accordion-tabs__content__heading-anchor:eq(${sessionParentAnchorId})`
                        ).attr({
                            'aria-expanded': 'false',
                            'aria-selected': 'false',
                            class:
                                'accordion-tabs__content__heading-anchor inactive',
                        });
                    } else {
                        notfirstAnchorContent.show();
                    }
                }); // End mobile tab click            
            } // End if parent is accordion-tab
        }); // End matching data-id values loop
    } // End if sessionParentAnchorId, i.e. parent accordion/tab click in session data

    // On anchor click: Set internal anchor tabindex values based on accordion state to aid link tabbing UX
    allAnchors.on('click', function(evt) { 
        evt.preventDefault();

        const anchorId = $(this).attr('id');
        const accordionStatus = $(this).attr('aria-expanded');
        const anchorType = $(this).parent().attr('class'); 

        // Accordion
        if(anchorType === 'accordion__content'){
            if (accordionStatus === 'true') {
                // removing hash from address bar when closing an accordion
                history.pushState({}, null, location.href.split('#')[0]);
            } else {
                // adding accordion hash to address bar
                window.location.hash = anchorId;
            }
        }

        // Tab
        if(anchorType === 'accordion-tabs__menu'){
            window.location.hash = anchorId;
        }

        if (this.parentNode.classList.contains('accordion__content')) {
            let relContent = this.nextSibling;
            let relContentLinks = relContent.querySelectorAll('a');
            if (this.classList.contains('inactive')) {
                relContent.classList.toggle('inactive', 'active');
                for (const r of relContentLinks) {
                    r.setAttribute('tabindex', '0');
                }
            } else if (this.classList.contains('active')) {
                relContent.classList.toggle('inactive', 'active');
                for (const r of relContentLinks) {
                    r.setAttribute('tabindex', '-1');
                }
            }
        }

        let anchor = $(this);

        let isAccordionAnchor = anchor.attr('data-accordion-anchor');

        if (anchor.hasClass('inactive')) {
            let id = anchor.attr('data-id');
            activateItem(id, isAccordionAnchor ? 'accordion' : 'tab');
            if (!isAccordionAnchor) {
                headingAnchors.filter(`[data-id="${id}"]`).focus();
            }
        } else if (isAccordionAnchor) {
            deactivateAll(allAnchors);
        }
    });

    // On page load: Set internal anchor tabindex values based on accordion state to aid link tabbing UX
    const setContentClass = () => {
        for (const a of allAnchors) {
            if (a.parentNode.classList.contains('accordion__content')) {
                if (a.classList.contains('inactive')) {
                    a.nextSibling.classList.toggle('inactive', 'active');
                } else if (a.classList.contains('active')) {
                    a.nextSibling.classList.toggle('active', 'inactive');
                }
            }
        }
    };
    setContentClass();

    // If accordion anchor closed, set associated content group anchors to tabindex '-1' so users can tab to next accordion anchor
    const setContentAnchorTabIndex = () => {
        const hiddenContent = document.querySelectorAll(
            `[class='accordion__content__group inactive']`
        );
        for (const h of hiddenContent) {
            let anchors = h.getElementsByTagName('a');
            for (const a of anchors) {
                let att = document.createAttribute('tabindex');
                att.value = '-1';
                a.setAttributeNode(att);
            }
        }
    };
    setContentAnchorTabIndex();

    if (activateInitial !== null) {
        activateItem(activateInitial, null);
    }

    return activateItem;
}