import $ from 'jquery';
import findHeadings from './common/prepare-sections';
import prepareContent from './common/prepare-content';
import prepareMenu from './common/prepare-menu';
import prepareLinks from './common/prepare-links';
import { contentCn } from './common/element-classnames';

const className = 'accordion-tabs';

function launch(el, i) {
    let widget = $(el);
    let contentWrapper = prepareContent(className, widget);
    const accordionId = $(el).attr('id');
    let headings = findHeadings(className, widget, contentWrapper);

    let menu = prepareMenu(
        className,
        widget,
        i,
        headings,
        contentWrapper,
        true,
        accordionId
    );
    prepareLinks(widget, headings, menu, headings.length > 0 && headings[0].id);
}

export default {
    launch,
    className,
};
