import $ from 'jquery';

function launch(el) {
    let widget = $(el);
    let select = widget.children('select');
    let fadeTime = 3000;

    widget.on('change', 'select', () => {
        let secondary = select
            .children()
            .eq(select.prop('selectedIndex'))
            .attr('data-secondary');
        if (secondary) {
            let secondaryItems = secondary.split('|');
            secondaryItems.shift();

            secondaryItems.forEach(function(item, index) {
                let $secondaryElement = $(
                    '.primary-dropdown-secondary-' + (index + 1)
                );

                if ($secondaryElement.hasClass('primary-dropdown-fade')) {
                    $secondaryElement.fadeOut(function() {
                        $(this)
                            .text(secondaryItems[index])
                            .fadeIn(fadeTime);
                    });
                } else {
                    if ($secondaryElement.hasClass('primary-dropdown--href')) {
                        $secondaryElement.attr('href', secondaryItems[index]);
                    } else {
                        $secondaryElement.text(secondaryItems[index]);
                    }
                }
            });
        }
    });
}

const className = 'primary-dropdown';

export default {
    launch,
    className,
};
