import $ from 'jquery';
import NavMobile from './nav-mobile';
import { tree, current, currentURL } from './parse-nav';
import HomeLink from './home-link';

function launch(el) {
    let element = $(el);

    const containerTop = element.find('.nav-mobile__content');
    const containerTri = element.find('.nav-mobile__tertiary__children');

    if (currentURL.includes('ac.uk/staff-hub') || currentURL.includes('ac.uk/alumni')) {  // Staffhub(SH) and Alumni uses different tree due to being type2 asset in Matrix
        let nav = new NavMobile(element, tree, current, containerTri);
        nav.launch();
        HomeLink(containerTop);
    } 
    else {
        let nav = new NavMobile(element, tree, current, containerTop);
    }
}

export default {
    className: 'nav-mobile',
    launch,
};
