import $ from 'jquery';
import Cookies from 'js-cookie';

const COOKIE_NAME = 'mbapopup';
const USERNAME_COOKIE = 'CityUsername';
const ALWAYS_SHOW_TO = 'sbbh844'; // always pop up if Khus is logged in
const inOneYear = new Date(new Date().getTime() + 365 * 24 * 60 * 60 * 1000);

function launch(el) {
    const widget = $(el);
    var displayPopup = false;

    if (
        Cookies.get(USERNAME_COOKIE) &&
        Cookies.get(USERNAME_COOKIE).match(new RegExp(ALWAYS_SHOW_TO))
    ) {
        displayPopup = true;
    } else {
        if (!Cookies.get(COOKIE_NAME)) {
            Cookies.set(COOKIE_NAME, '0', { expires: inOneYear });
        } else {
            if (Cookies.get(COOKIE_NAME) === '0') {
                // && Math.random() <= 0.2) {
                Cookies.set(COOKIE_NAME, '1', { expires: inOneYear });
                displayPopup = true;
            }
        }
    }

    $('.mba-popup').click(function(e) {
        if (e.target !== this) return;

        $('.mba-popup').removeClass('open');
        e.preventDefault();
    });

    widget.find('.mba-popup__form__close').click(function(e) {
        $('.mba-popup').removeClass('open');
        e.preventDefault();
    });

    if (displayPopup) {
        $('.mba-popup').addClass('open');
    }
}

const className = 'mba-popup';

export default {
    launch,
    className,
};
