import $ from 'jquery';

function launch(el) {
    let widget = $(el);
    let select = widget.children('select');

    widget.on('change', 'select', () => {
        let url = select
            .children()
            .eq(select.prop('selectedIndex'))
            .attr('data-url');
        if (url) {
            window.location.href = url + window.location.search;
        }
    });
}

const className = 'url-dropdown';

export default {
    launch,
    className,
};
