import $ from 'jquery';
import getDataAttributes from '../../../js-utils/get-data-attrs';
import loadYoutubeVideo from '../../video-preview/video-preview-youtube';

function getService(link) {
    let href = link.attr('href') || '';
    let params = getDataAttributes(link);

    let ytMatch = href.match(
        /(?:https?:\/\/)?(?:www\.)?youtu\.?be(?:\.com)?\/?.*(?:watch|embed)?(?:.*v=|v\/|\/)([\w\-_]+)\&?/
    );
    if (ytMatch) {
        return { type: 'youtube', id: ytMatch[1], params };
    }

    return null;
}

function launch(el) {
    let link = $(el);
    let service = getService(link); // object with youtube id
    let figcaption = link.find('.video-preview--caption');

    if (service) {
        let parent = link.parent();
        let wrapper = null;

        if (parent.hasClass('video-preview')) {
            wrapper = parent;
        } else {
            wrapper = $('<div/>')
                .addClass('video-preview')
                .insertBefore(link);
            wrapper.append(link);
        }

        link.on('click', event => {
            loadVideo(wrapper, service);
            event.preventDefault();
            wrapper.addClass('video-container');

            let dlg = $('.video-banner--popup')
                .addClass('video--playing')
                .addClass('fallout')
                .addClass('fallout--both');

            let closeButton = $(
                '<button class="dialog__close--video-popup"> <span class="fa fa-times" aria-hidden="true"></span></button>'
            )
                .attr('type', 'button')
                .appendTo(dlg);

            let caption = $(
                '<figcaption>' + figcaption.html() + '</figcaption>'
            ).appendTo(dlg);

            figcaption.remove();

            closeButton.on('click touchstart', function() {
                $('iframe').remove();
                dlg.removeClass('video--playing')
                    .removeClass('fallout')
                    .removeClass('fallout--both');
                $('.video-popup__img').css('display', 'block');
                $('.video-popup__button').css('display', 'block');
                figcaption.html();
                wrapper.removeClass('video-container');
                closeButton.remove();
                caption.remove();
                $('.video-popup__button').append(figcaption);
            });

            $(document).on('keydown', function(e) {
                if (e.keyCode === 27) {
                    // ESC
                    $('iframe').remove();
                    dlg.removeClass('video--playing')
                        .removeClass('fallout')
                        .removeClass('fallout--both');
                    $('.video-popup__img').css('display', 'block');
                    $('.video-popup__button').css('display', 'block');
                    wrapper.removeClass('video-container');
                    closeButton.remove();
                    caption.remove();
                }
            });
        });
    }
}

function loadVideo(wrapper, service) {
    switch (service.type) {
        case 'youtube':
            return loadYoutubeVideo(wrapper, service);
        default:
            return false;
    }
}

export default {
    className: 'video-popup__button',
    launch,
};
