import $ from 'jquery';

function launch(el) {
    const slider = $(el);
    const viewportWidth = $(window).width();
    let slideCount = slider.find('li').length;
    let slides = slider.find('li');
    const slidesContainer = slider.find('ul');
    let strTouchX;
    let endTouchX;

    if (viewportWidth <= 900 && slideCount > 1) {
        // implements slider

        // generates slide indicators
        slidesContainer.after(
            '<div class="slider__indicators__wrap"><div class="slider__indicators"></div></div>'
        );
        slides.each(function() {
            slider
                .find('.slider__indicators')
                .append('<div class="slider__indicators__indicator"></div>');
        });
        slider
            .find('.slider__indicators__indicator')
            .eq(0)
            .addClass('slider__indicators__indicator--active');

        // records touches
        slider.on('touchstart', function(e) {
            strTouchX = e.touches[0].clientX;
        });

        slider.on('touchend', function(f) {
            endTouchX = f.changedTouches[0].clientX;
            slideDirection(endTouchX);
        });

        // detects slide direction. 5px movement deadzone has been allowed for swipe sensitivity. Slider will only react to swipes bigger than 5px in length
        function slideDirection(endTouchX) {
            endTouchX > strTouchX + 5
                ? slide('+')
                : endTouchX < strTouchX - 5
                ? slide('-')
                : null;
        }
        // handels slide
        function slide(dir) {
            let slideContainerPosition = parseInt(slidesContainer.css('left'));
            let slideWidth = parseInt(slides.css('width'));
            let animated = slider.find('ul:animated').length;
            let slideIndicator = slider.find('.slider__indicators__indicator--active').index();

            if (
                dir === '-' &&
                slideContainerPosition - slideWidth >=
                    -(slideWidth * (slideCount - 1)) &&
                animated === 0
            ) {
                slidesContainer.animate(
                    { left: '' + dir + '=' + slideWidth + '' },
                    'slow',
                    indicatorPosition(slideIndicator + 1, slideWidth)
                );
            } else if (
                dir === '+' &&
                slideContainerPosition + slideWidth <= 0 &&
                animated === 0
            ) {
                slidesContainer.animate(
                    { left: '' + dir + '=' + slideWidth + '' },
                    'slow',
                    indicatorPosition(slideIndicator - 1, slideWidth)
                );
            }
        }
        // slide index indicators
        slider.find('.slider__indicators__indicator').on('click', function() {
            let slideWidth = parseInt(slides.css('width'));
            let slideIndex = $(this).index();
            let slideContainerPosition = slideWidth * slideIndex;

            slidesContainer.animate(
                { left: -slideContainerPosition },
                'slow',
                indicatorPosition(slideIndex)
            );
        });

        // highlights which slide you on
        function indicatorPosition(value) {
            slider.find('.slider__indicators__indicator').each(function() {
                if ($(this).index() == value) {
                    $(this).addClass('slider__indicators__indicator--active');
                } else {
                    $(this).removeClass(
                        'slider__indicators__indicator--active'
                    );
                }
            });
        }

        $(window).on('orientationchange', function() {
            setTimeout(function() {
                let slideIndicator = $(
                    '.slider__indicators__indicator--active'
                ).index();
                let slideWidth = parseInt(slides.css('width'));
                let slideCorrection = -(slideIndicator * slideWidth);
                slidesContainer.animate({ left: slideCorrection }, 1);
            }, 200);
        });
    } // end if
}

const className = 'slider-news';

export default {
    launch,
    className,
};
