import $ from 'jquery';
import { openModalDialog } from '../dialog/dialog';
import {
    applyVariations,
    identifyVariations,
    variationClassNames,
} from '../../js-utils/variations';

const className = 'popup-def-list-group';

function findItems(widget) {
    let items = [];
    widget.find('li').each(function() {
        let li = $(this);
        let children = li.children();
        if (children.length === 2) {
            let heading = li.children().first();
            if (
                heading.length > 0 &&
                heading
                    .prop('tagName')
                    .toLowerCase()
                    .match(/h[0-9]/)
            ) {
                let content = children.eq(1);
                let dlgContent = content.clone();
                content.addClass('popup-def-list-group__content');
                let categories = li
                    .parentsUntil(widget)
                    .filter('[data-popup-def-list-category]')
                    .get()
                    .reverse()
                    .map(e => $(e).attr('data-popup-def-list-category'));

                items.push({
                    li,
                    heading,
                    title: heading.html(),
                    content: dlgContent,
                    category:
                        categories.length > 0 ? categories.join(' | ') : null,
                });
            }
        }
    });
    return items;
}

function prepareItems(widget, items) {
    let variations = identifyVariations(widget, className);

    let open = (i, identifyFocusBackTo) => {
        let infinateLoop 
        let { title, category, content } = items[i];

        let dlgTitle = $('<div></div>');
        if (category) {
            $('<div></div>')
                .addClass('popup-def-list-group__dlg__title__small')
                .html(category)
                .appendTo(dlgTitle);
        }

        $('<div></div>')
            .html(title)
            .appendTo(dlgTitle);

        let prevIndex = (i - 1 + items.length) % items.length;
        let nextIndex = (i + 1) % items.length;
        let prev = items[prevIndex];
        let next = items[nextIndex];

        let prevLink = $('<a></a>')
            .attr('href', '#')
            .append(prev.title)
            .on('click', evt => {
                evt.preventDefault();
                open(prevIndex, false);
            });

        let nextLink = $('<a></a>')
            .attr('href', '#')
            .append(next.title)
            .on('click', evt => {
                evt.preventDefault();
                open(nextIndex, false);
            });

        let nextWrapper = $('<div></div>')
            .addClass('popup-def-list-group__prevnext__next')
            .append(nextLink)
            .append(
                '<span class="popup-def-list-group__prevnext__next__icon" aria-hidden="true"></span>'
            );

        let prevWrapper = $('<div></div>')
            .addClass('popup-def-list-group__prevnext__prev')
            .append(
                '<span class="popup-def-list-group__prevnext__prev__icon" aria-hidden="true"></span>'
            )
            .append(prevLink);

        let dlgContent = $('<div></div>').append(content);

        let prevNextWrapper = $('<div></div>')
            .addClass('popup-def-list-group__prevnext');
        if(i > 0){
            prevNextWrapper.append(prevWrapper);
        }

        if(nextIndex !== 0){
            prevNextWrapper.append(nextWrapper);
        }
        
        prevNextWrapper.appendTo(dlgContent);

        openModalDialog(dlgTitle, dlgContent, {
            identifyFocusBackTo,
            className: variationClassNames(`${className}__dlg`, variations),
        });
    };

    items.forEach(({ heading }, i) => {
        let anchor = $('<a></a>')
            .attr('href', '#')
            .addClass('popup-def-list-group__anchor')
            .html(heading.html());

        anchor.on('click', evt => {
            evt.preventDefault();
            open(i, true);
        });

        heading.empty().append(anchor);
    });
}

function launch(el) {
    let widget = $(el);
    let items = findItems(widget);
    prepareItems(widget, items);
}

export default {
    launch,
    className,
};
