const dataBrowser = [
    { subString: 'Edge', identity: 'Edge' },
    { subString: 'MSIE', identity: 'IE' },
    { subString: 'Trident', identity: 'IE' },
    { subString: 'Firefox', identity: 'Firefox' },
    { subString: 'Opera', identity: 'Opera' },
    { subString: 'OPR', identity: 'Opera' },
    { subString: 'Chrome', identity: 'Chrome' },
    { subString: 'Safari', identity: 'Safari' },
];

function identity() {
    let dataString = navigator.userAgent;
    for (let i = 0; i < dataBrowser.length; i++) {
        if (dataString.indexOf(dataBrowser[i].subString) >= 0) {
            return dataBrowser[i];
        }
    }

    return null;
}

export function browserDetect() {
    let id = identity();
    let browser = 'Other';
    let version = -1;

    if (id !== null) {
        browser = id.identity;

        let rv = navigator.userAgent.indexOf('rv:');
        if (id.subString === 'Trident' && rv >= 0) {
            version = parseFloat(navigator.userAgent.substring(rv + 3));
        } else {
            version = parseFloat(
                navigator.userAgent.substring(
                    navigator.userAgent.indexOf(id.subString) +
                        id.subString.length +
                        1
                )
            );
        }
    }

    return { browser, version };
}
