import $ from 'jquery';

const dataAttrRegex = /^data-(.+)$/;

export default function getDataAttributes(node) {
    let attrs = {};
    $.each(node[0].attributes, function(index, attribute) {
        let match = attribute.name.match(dataAttrRegex);
        if (match) {
            attrs[match[1]] = attribute.value;
        }
    });

    return attrs;
}
