import $ from 'jquery';

export default function scrollTo(element) {
    element[0].scrollIntoView(true);

    let maxHeight = 0;

    $('.js-is-stuck').each(function() {
        let height = $(this).height();
        if (height > maxHeight) {
            maxHeight = height;
        }
    });

    if (maxHeight > 0) {
        let scrolledY = window.scrollY;

        if (scrolledY) {
            window.scroll(0, scrolledY - maxHeight);
        }
    }
}
