import actionOnScroll from '../action-on-scroll';
import browserSupport from '../../js-utils/browser-support';
import $ from 'jquery';
const className = 'block-links-v24__heading';

function launch (element){
  if (browserSupport.intersectionObserver) {

    const easeElements = [].slice.call(element.querySelectorAll('.block-links-v24__heading__text'));
  
    easeElements.forEach(e =>{
      actionOnScroll($(e), 'block-links-v24__heading__text--animate', false, { threshold: 1 });
    });
  }
}

export default {
    launch,
    className,
};