import $ from 'jquery';

(() => {
    $("iframe[src*='youtube']").each(function() {
        var $element = $(this);
        var $parent = $element.parent();

        // don't double wrap an iframe
        if (
            !$parent.hasClass('video-preview') &&
            !$parent.hasClass('video-preview--loading') &&
            !$parent.hasClass('media') &&
            !$parent.hasClass('media--youtube')
        ) {
            $element.wrap('<div class="media media--youtube"></div>');
        }
    });
})();
