import $ from 'jquery';

function launch(el) {
    var attachments = $(el);

    if (attachments.length) {
        var template =
            '<li class="cass-knowledge-attachment__item"><a href="$0">$1</a></li>';
        var attachmentLinks = '<ul class="cass-knowledge-attachment__listing">';

        attachments = attachments
            .html()
            .trim()
            .split('; ');

        $.each(attachments, function(index, value) {
            var parts = value.slice(1, -1).split('}{');
            var attachmentLink = template
                .replace('$0', parts[1])
                .replace('$1', parts[0]);
            attachmentLinks = attachmentLinks.concat(attachmentLink);
        });

        attachmentLinks = attachmentLinks.concat('</ul>');
        $('.cass-knowledge-attachment__raw').html(attachmentLinks);
    }
}

const className = 'cass-knowledge-attachment__raw';

export default {
    launch,
    className,
};
