import $ from 'jquery';
import findHeadings from './common/prepare-sections';
import prepareContent from './common/prepare-content';
import prepareLinks from './common/prepare-links';

function launch(el, i) {

    let headingLevel;
    let accordionId = $(el).attr('id');
   
    $.each(el.attributes, function() {
        this.name === 'data-level' ? headingLevel = this.value : null;
    });

    let widget = $(el);
    let contentWrapper = prepareContent(className, widget);
    let headings = findHeadings(className, widget, contentWrapper, headingLevel, accordionId);

    prepareLinks(widget, headings, null);

    const hash = window.location.hash;

    // on page load if accordion hash is found inside address bar then find the accordion and open it by simulating click
    if(hash){
        $(hash).trigger('click');
    }
}

const className = 'accordion';

export default {
    launch,
    className,
};
