import $ from 'jquery';
import getDataAttributes from '../../js-utils/get-data-attrs';
import loadYoutubeVideo from './video-preview-youtube';

function getService(link) {
    let href = link.attr('href') || '';
    let params = getDataAttributes(link);

    let ytMatch = href.match(
        /(?:https?:\/\/)?(?:www\.)?youtu\.?be(?:\.com)?\/?.*(?:watch|embed)?(?:.*v=|v\/|\/)([\w\-_]+)&?/
    );
    if (ytMatch) {
        return { type: 'youtube', id: ytMatch[1], params };
    }

    return null;
}

function launch(el) {
    let link = $(el);
    let service = getService(link);

    if (service) {
        let parent = link.parent();
        let wrapper = null;

        if (parent.hasClass('video-preview')) {
            wrapper = parent;
        } else {
            wrapper = $('<div/>')
                .addClass('video-preview')
                .insertBefore(link);
            wrapper.append(link);
        }

        link.on('click', event => {
            loadVideo(wrapper, service);
            event.preventDefault();
        });
    }
}

function loadVideo(wrapper, service) {
    switch (service.type) {
        case 'youtube':
            return loadYoutubeVideo(wrapper, service);
        default:
            return false;
    }
}

$('body').keyup('keyup', function(e) {
    // If user tabs into video preview
    if (e.keyCode == 9) {
        let playIcon = $('.video-preview__button');
        playIcon.is(':focus')
            ? playIcon.addClass('in-focus')
            : playIcon.removeClass('in-focus');
    }
});

export default {
    className: 'video-preview__button',
    launch,
};
