import $ from 'jquery';
import extend from 'extend';
import actionOnScroll from '../action-on-scroll';
import browserSupport from '../../js-utils/browser-support';
import animationTypes from './animation-types';

function getAnimData(prevData, element, ...bases) {
    const data = {};

    bases.forEach(base => {
        const regex = new RegExp(`^data-${base}-(.+)$`);
        const el = element.get(0);
        if (el && el.attributes) {
            for (let { name, value } of el.attributes) {
                const match = name.match(regex);
                if (match) {
                    data[match[1]] = value;
                }
            }
        }
    });

    return prevData ? extend({}, prevData, data) : data;
}

export function launchAnimation(widget, ...types) {
    const widgetParams = getAnimData(null, widget, 'anim');

    const anims = types
        .filter(type => animationTypes[type])
        .map(type => {
            const anim = animationTypes[type];
            const isFunction = typeof anim === 'function';
            const func = isFunction ? anim : anim.run;
            const typeParams = getAnimData(
                widgetParams,
                widget,
                `anim-${type}`
            );
            const targets = typeParams.target
                ? $.makeArray(widget.find(`.${typeParams.target}`)).map(
                      targetEl => {
                          const target = $(targetEl);
                          return {
                              target,
                              params: getAnimData(
                                  typeParams,
                                  target,
                                  'anim',
                                  `anim-${type}`
                              ),
                          };
                      }
                  )
                : [{ target: widget, params: typeParams }];

            const okTargets = targets.filter(
                ({ target, params }) =>
                    isFunction || !anim.init || anim.init(target, params)
            );
            return (
                okTargets.length > 0 &&
                (() => {
                    okTargets.forEach(({ target, params }) =>
                        func(target, params)
                    );
                })
            );
        })
        .filter(f => !!f);

    if (anims.length > 0) {
        const runAnimations = () => anims.forEach(anim => anim());
        if (browserSupport.intersectionObserver) {
            actionOnScroll(widget, runAnimations);
        } else {
            runAnimations();
        }
    }
}
