import {
    current as globalCurrent,
    tree as globalTree,
    currentURL
} from '../nav-mobile/parse-nav';

function processTree(gCurrent, gTree) {

    if (currentURL.includes('ac.uk/staff-hub') && gCurrent.length < 1) {
         // left hand navigation only starts on level 2 if Staffhub
        return { tree: null, current: null };  
    } else if  (currentURL.includes('ac.uk/alumni') && gCurrent.length < 1) {
        // left hand navigation only starts on level 2 if Alumni
        return { tree: null, current: null };
    } 
    else if (!currentURL.includes('ac.uk/staff-hub') && gCurrent.length < 3) {
        // left hand navigation only starts on level 3
        return { tree: null, current: null };  
    }

    const tree = gCurrent
        .slice(1, 3)
        .reduce(
            (acc, currentItem) =>
                acc.children.find(child => child.url === currentItem.url),
            gTree
        );
    const current = gCurrent.slice(2);

    return { current, tree };
}

const { tree, current } = processTree(globalCurrent, globalTree);

export { tree, current,  currentURL };
