import $ from 'jquery';

const homeURL = 'https://' + window.location.hostname;

const HomeLink = function(e) {
    const placeholder = $(e).find('.nav-mobile__tertiary');

    let wrapper = $('<div></div>')
        .addClass('vertical-menu')
        .addClass('vertical-menu--l0')
        .prependTo(placeholder);

    let container = $('<div></div>')
        .addClass('vertical-menu__header')
        .appendTo(wrapper);

    $('<a class="vertical-menu__page-link"></a>')
    .attr('href', `${homeURL}`)
    .append(
        $(
            '<span class="vertical-menu__page-link__label"></span>'
        ).html('Bayes Business School')
    )
    .appendTo(container);
};

export default HomeLink;
