/**
 * Turn a query string into an object.
 *
 * @param {string} parameterString - An HTML query string.
 * @returns {object} An object containing the parameters.
 */
export function parametersToObject(parameterString) {
    const parameters = {};
    parameterString
        .substr(1)
        .split('&')
        .forEach((parameter) => {
            const [k, v] = parameter.split('=');
            parameters[k] = v;
        });
    return parameters;
}

/**
 * Turn an object into a query string.
 *
 * @param {object} parameterObj - An object containing the parameters and their values.
 * @returns {string} A string of the parameters.
 */
export function objectToParameters(parameterObj) {
    if (Object.keys(parameterObj).length > 0) {
        let parameters = '?';
        for (const key in parameterObj) {
            parameters.length !== 1 && (parameters += '&');
            parameters += key + '=' + parameterObj[key];
        }
        return parameters;
    } else {
        return '';
    }
}


/**
 * Check browser user agent is IE or edge and return version number
 */
export function detectIE() {
    const ua = window.navigator.userAgent,
        versionLength = 10,
        msie = ua.indexOf('MSIE '),
        msieVersionOffset = 5;

    if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(
            ua.substring(msie + msieVersionOffset, ua.indexOf('.', msie)),
            versionLength
        );
    }

    const trident = ua.indexOf('Trident/'),
        tridentVersionOffset = 3;

    if (trident > 0) {
        // IE 11 => return version number
        let rv = ua.indexOf('rv:');
        return parseInt(
            ua.substring(rv + tridentVersionOffset, ua.indexOf('.', rv)),
            versionLength
        );
    }

    const edge = ua.indexOf('Edge/'),
        edgeVersionOffset = 5;

    if (edge > 0) {
        // Edge (IE 12+) => return version number
        return parseInt(
            ua.substring(edge + edgeVersionOffset, ua.indexOf('.', edge)),
            versionLength
        );
    }

    // other browser
    return false;
}
