import $ from 'jquery';
import {
    contentGroupCn,
    headingAnchorCn,
    headingAnchorIconCn,
    headingCn,
} from './element-classnames';

function prepareAccordionHeading(patternClassname, text, id, headingLevel, accordionId) {

    let anchor = 
    
    $('<a/>')
        .addClass(`${headingAnchorCn(patternClassname)} inactive`)
        .attr('href', '#')
        .attr('data-id', id)
        .attr('id', `accordion-${accordionId}-header-${id}`)
        .attr('data-accordion-anchor', true);
   
    $('<h' + headingLevel + '/>')
        .addClass('accordion__content__heading-anchor__label')
        .html(text)
        .appendTo(anchor);

    $('<span/>')
        .attr('aria-hidden', 'true')
        .addClass(headingAnchorIconCn(patternClassname))
        .appendTo(anchor);

    return anchor;
}

export default function findHeadings(className, widget, contentWrapper, headingLevel, accordionId) {

    let firstHeading = contentWrapper.find('h2:not(.dropdown-heading),h3,h4,h5'),
        headingTagName = firstHeading.prop('tagName'),
        tabHeadings = [],
        accordionHeadings = [];

    contentWrapper.parent().attr('class') === 'accordion'
        ? accordionHeadings = contentWrapper.find(`.accordion__heading`).toArray()
        : tabHeadings = contentWrapper.find(`${headingTagName}:not(.dropdown-heading)`).toArray();

    const allHeadings = tabHeadings.concat(accordionHeadings);

    return (allHeadings).map((element, i) => {
        let header = $(element)
            .attr('data-id', i)
            .addClass(headingCn(className));

        let parents = header.parentsUntil(contentWrapper);
        let wrapper = parents.length > 0 ? parents.last() : null;
    
        if (!wrapper) {
            let contentElements = [];
            let next = header.next();
            while (next.length > 0 && next.prop('tagName') !== headingTagName) {
                contentElements.push(next);
                next = next.next();
            }

            wrapper = $('<div></div>')
                .insertBefore(header)
                .append(header)
                .append(contentElements);
        }

        wrapper.addClass(contentGroupCn(className));

        let anchor = prepareAccordionHeading(className, header.html(), i, headingLevel, accordionId);

        anchor.insertBefore(wrapper);

        return {
            id: i,
            text: header.html(),
            anchor,
        };
    });
    
}
