import actionOnScroll from '../action-on-scroll';
import browserSupport from '../../js-utils/browser-support';
import $ from 'jquery';
const className = 'bayes-home';

function launch (element){
  if (browserSupport.intersectionObserver) {
    
    const easeElements = [].slice.call(element.querySelectorAll('.ease-into-view'));
  
    easeElements.forEach(e =>{
      actionOnScroll($(e), 'ease-into-view-completed', false, { threshold: 0.3 });
    });
  }
}

export default {
    launch,
    className,
};